body {
  background-image: url("./Images/papyrus-dark.png");
}

footer {
  margin-top: 20px;
}

textarea {
  resize: none;
}


main {
  padding-left: 20px;
}

h1 {
  margin-top: 10px;
  font-family: 'Raleway', sans-serif;
}

h5 {
  text-decoration: underline;
}

hr{
  border: 0;
  height: 1px;
  background-image: linear-gradient(to right, rgba(0, 0, 0, 0), rgba(0, 0, 0, 0.75), rgba(0, 0, 0, 0));
}

.portfolioItem {
  margin-bottom: 15px;
  
}

.portfolioItem p {
  margin-bottom: 0;
  font-family: 'Ubuntu', sans-serif;
}

.portfolioItem a {
  color: black;
  text-decoration: underline;
}

.contactLine {
  margin-bottom: 20px;
}

.contactLine h5 {
  font-family: 'Ubuntu', sans-serif;
}

/* Way to keep footer from being mid page */
.middlePage {
  min-height: 100vh;
}

.pageContainer {
  position: relative;
}

.portfolioImage {
  margin-right: 15px;
  margin-bottom: 15px;
  margin-top: 10px;
  min-width: 200px;
  max-width: 300px;
  max-height: 200px;
  min-height: 200px;
}

#myNavbar {
  font-family: 'Raleway', sans-serif;
}

#myName {
  padding-top: 5px;
  margin-bottom: 0;
}

#aboutMeImage {
  margin-right: 15px;
  min-width: 200px;
  box-shadow: 1px 1px 8px black;
}

#bioText {
  padding: 0 10px 10px 20px;
  text-align: left;
  font-size: 18px;
  font-family: 'Ubuntu', sans-serif;
}

#aboutMe, #portfolio, #contact {
  background-color: blanchedalmond;
  margin-top: 80px;
  border: 1px solid black;
  min-width: 500px;
  padding-bottom: 15px;
}

#submitButton {
  border-radius: 1em;
}

#submitButton:hover { 
  opacity: .8;
}

button.navbar-toggler:hover {
  opacity: .6;
}

/* Media queries */

@media screen and (max-width: 768px) {
  main {
      margin-right: 20px;
      margin-left: 20px;
      padding-right: 0;
      padding-left: 0;
  }
  #aboutMeImage {
      max-width: 250px;
      min-width: 0;
  }
  #aboutMe, #portfolio, #contact{
      min-width: 0;
  }

}

@media screen and (max-width: 320px) {
  main {
      margin-right: 10px;
      margin-left: 10px;
  }
  #aboutMeImage {
      max-width: 100%;
      margin-bottom: 20px;
  }
}